import './style.scss'
import Like from './images/like.png'
import LikeActive from './images/like_active.png'
import Search from './images/search.png'
import Fire from './images/fire.png'
import {useGlobal} from "../../state/global/hooks";
import React, {useState} from "react";
import {useLocal} from "../../state/local/hooks";
import {useActiveWeb3React} from "../../hooks";
import {CHAIN_NAME} from "../../data/const";
import {formatAmount} from "../../utils";
import Icon from "../Icon";
import {useView} from "../../state/view/hooks";

type LikeIconProps<T> = {
  value: T,
  size?: number,
  active?: boolean,
  onClick?: (key: T) => void
}

export const LikeIcon: React.FC<LikeIconProps<any>> = ({value, size= 20,  active = false, onClick}) => {
  return (
    <img className='like-icon' width={size} src={active ? LikeActive : Like} alt="" onClick={() => {onClick && onClick(value)}}/>
  )
}

const mostSearches = ['BTC', 'ETH', 'USDT', 'DAI']

type Props = {
  onChange?: () => void
}
const SearchCurrency: React.FC<Props> = ({onChange}) => {
  const {selectedCurrency, tokenList} = useGlobal()
  // const [value, setValue] = useState(selectedCurrency ? selectedCurrency.name : '')
  const {setSelectedToken, toggleLikedToken, likedTokens} = useLocal()
  const [value, setValue] = useState('')
  const { chainId } = useActiveWeb3React()
  const {isMobile} = useView()

  // console.log('tokenList=====')
  // console.log(tokenList)
  return (
    <div className='searchCurrency'>
      <div className="top">
        <div className='search-row'>
          <div className='input'>
            <input type="text" value={value} onChange={(e) => {
              let value = e.target.value.trim()
              setValue(value)
            }} placeholder='Search Market' autoFocus={!isMobile} />
            <button><img src={Search} alt=""/></button>
          </div>
          {/*<p className='right'>Collection</p>*/}
          {/*<LikeIcon value='' />*/}
        </div>
        <div className='most'>
          Most Searches:{mostSearches.map(item => (
          <button
            key={item}
            onClick={() => {
              setValue(item)
            }}
          >{item}</button>
        ))}
        </div>
        <div className='title'><img src={Fire} alt=""/>Top Volume</div>
        <div className='table-header'>
          <div>Name</div>
          <div>Volume,24h</div>
          <div>Liquidity</div>
          <div>Price</div>
          <div>&nbsp;</div>
        </div>
      </div>
      <div className="bottom">
        {tokenList
          .filter(({name}) => !value || name.toLowerCase().includes(value.toLowerCase()))
          .map(({name, symbol, logo, cid, address, volume, decimal, liquidity, price, delta}) => {

            const active = address === selectedCurrency?.address;
            const setToken = () => {
              // !active && setSelectedToken(name)
              if (!active) {
                setSelectedToken(address)
                onChange && onChange()
              }
            }
            const toggleToken = () => {
              toggleLikedToken(address)
            }
            const isUp = !delta.startsWith('-')
            return (
              <div key={address} className={`table-row ${active ? 'active' : ''}`}>
                <div onClick={setToken}>
                  <img src={logo} alt=""/>
                  <p>{symbol} {chainId as number !== cid && <><br /><i className='chain_name'>{CHAIN_NAME[cid]}</i></>}</p>
                </div>
                <div onClick={setToken}>{formatAmount(volume, decimal, 2)}</div>
                <div onClick={setToken}>{formatAmount(liquidity, decimal, 2)}</div>
                <div onClick={setToken}>${price}<div className={isUp ? 'up' : 'down'}><Icon symbol={isUp ? 'up' : 'down'} size={14} />{delta}</div></div>
                <div onClick={toggleToken}>
                  <LikeIcon value={name} active={likedTokens.includes(address)}/>
                </div>
              </div>
            )
          })}
      </div>
    </div>
  )
}

export default SearchCurrency
