import React, {useState, useMemo, useReducer, useEffect, useRef} from 'react'
import './index.scss';
import cn from 'classnames';
import TokenList from '../component/TokenList'
import TradeHeader from "../component/TradeHeader";
import {useRequest} from "ahooks";
import { getLiquidityRecords, getTradeRecords} from "../../../service";
import {useList} from "../../../state/list/hooks";

type itemType = {
    title: string,
    filter: boolean
}

const SidebarConfig: { [name: string]: itemType } = {
    liquidity: {
        title: "Pool Activity",
        filter: true
    },
    trade: {
        title: "Trading History",
        filter: false
    }
}

type ConditionTypes = 0 | 1 | 2;
type LiquidityList = any[]
type LiquidityHistory = [LiquidityList, LiquidityList, LiquidityList];
type LiquidityAction = { type: ConditionTypes | -1, payload: LiquidityList }
type LiquidityReducer = React.Reducer<LiquidityHistory, LiquidityAction>
export type Condition = {text: string, type: ConditionTypes}
const initLiquidityHistory: LiquidityHistory = [[], [], []]
export const conditions: [Condition, Condition, Condition] = [
    { text: 'all', type: 0},
    { text: 'adds', type: 1},
    { text: 'removes', type: 2}
]

const reducer: LiquidityReducer = (state, action) => {
    if(action.type === -1) {
        state = initLiquidityHistory
        return state
    } else {
        state[action.type] = action.payload
        return [...state]
    }
}

const defaultRequestOptions = {
    manual: true,
    initialData: [],
    throttleInterval: 100,
    formatResult: (data: Result) => {
        if (data?.success) return data?.data || []
        return []
    }
}

export const useLiquidityRecords = (type: 0 | 1 | 2, active: boolean) => {
    const {CurrentSwapLiquidity} = useList()
    // console.log(CurrentSwapLiquidity?.address)
    const [liquidityRecords, dispatch] = useReducer<LiquidityReducer>(reducer, initLiquidityHistory);
    const {data, params, run, cancel} = useRequest(getLiquidityRecords, {
        ...defaultRequestOptions,
        pollingInterval: 6000,
        pollingWhenHidden: false
    })
    const ref = useRef<string>('')

    useEffect(() => {
        if (CurrentSwapLiquidity && ref.current !== type + CurrentSwapLiquidity.address) {
            const {address} = CurrentSwapLiquidity;
            if (active) run(address, type)
            ref.current = type + address
        } else {
            dispatch({type: -1, payload: []})
            // cancel()
        }

        return () => {
            cancel()
        }
    }, [CurrentSwapLiquidity, active, cancel, run, type])

    useEffect(() => {
        if (data && data.length) {
            // console.log('data++++++++')
            // console.log(data)
            dispatch({
                type: params[1] as ConditionTypes,
                payload: data,
            })
        }
    }, [data])

    // useEffect(() => {
    //    return () => {
    //         cancel()
    //     }
    // }, [cancel])


    return {liquidityRecords: liquidityRecords[type]}
}

export const useTradeRecords = (active: boolean) => {
    const {CurrentSwapLiquidity} = useList()
    const {data: tradeRecords, run, cancel} = useRequest(getTradeRecords, {
        ...defaultRequestOptions,
        pollingInterval: 6000,
        pollingWhenHidden: false
    })

    useEffect(() => {
        if (CurrentSwapLiquidity && active) {
            const {address} = CurrentSwapLiquidity;
            run(address)
        }

        return () => {
            cancel()
        }
    }, [CurrentSwapLiquidity, active, cancel, run])

    return {tradeRecords}
}

type keyType = 'liquidity' | 'trade'

const TokenSideBar: React.FC = () => {
    const [key, setKey] = useState<keyType>('liquidity')
    const [condition, setCondition] = useState<Condition>(conditions[0])
    const {liquidityRecords} = useLiquidityRecords(condition.type, key === 'liquidity')
    const {tradeRecords} = useTradeRecords(key === 'trade')
    const { title, filter } = useMemo(() => {
        return SidebarConfig[key]
    }, [key])

    return (
        <div className='sidebar'>
            <div className={cn('switch', { [key]: 1 })}>
                <button onClick={() => setKey('liquidity')}>Liquidity</button>
                <button onClick={() => setKey('trade')}>Trading</button>
            </div>
            <TradeHeader type={key} />
            <div className="pool-activity">
                <div className='pool'>
                    <span className='title'>{title}</span>
                    {filter && <div className='filter'>
                        {conditions.map((item) => (
                            <button
                                className={cn('tab-button', { active: item.type === condition.type })}
                                onClick={() => setCondition(item)} key={item.type}
                            >{item.text}</button>
                        ))}
                    </div>}
                </div>
                <TokenList currentTab={key} data={key === 'trade' ? tradeRecords : liquidityRecords} />
            </div>
        </div>
    )
}
export default TokenSideBar
